import { createStore, applyMiddleware } from 'redux';
import thunk from 'redux-thunk';
import rootReducer from './reducers/rootReducer';

import exerciseReducer from './reducers/exerciseReducer';

const sampleState = {
  exerciseReducer: {
    exercises: [],
    latestPassword: undefined
  }
}

export default function configureStore() {

  const persistedState = JSON.parse(localStorage.getItem('classics-is-cool') || JSON.stringify(sampleState));
  const newExerciseState = exerciseReducer(persistedState.exerciseReducer, {
    type: 'UNLOCK_EXERCISE',
    payload: persistedState.exerciseReducer.latestPassword
  });
  // TODO filter out keys not in the reducer
  const store = createStore(
    rootReducer,
    {
      ...persistedState,
      exerciseReducer: newExerciseState
    },
    applyMiddleware(thunk)
  );

  store.subscribe(() => {
    localStorage.setItem('classics-is-cool', JSON.stringify(store.getState()));
  });

  return store;
}

