export const addExercise = ({title, exercise}) => dispatch => {
  dispatch({
    type: 'ADD_EXERCISE',
    payload: { title, exercise }
  });
}

export const getExercises = () => dispatch => {
  dispatch({
    type: 'GET_EXERCISES'
  });
}

export const unlockExercise = password => dispatch => {
  dispatch({
    type: 'UNLOCK_EXERCISE',
    payload: password
  });
}