import React from 'react';
import { connect } from 'react-redux';

import { withStyles } from '@material-ui/core/styles';


import { Divider, Drawer, Hidden, List, ListSubheader, ListItem, ListItemIcon, ListItemText } from '@material-ui/core';

import AddIcon from '@material-ui/icons/Add';
import LockIcon from '@material-ui/icons/Lock';
import LockOpenIcon from '@material-ui/icons/LockOpen';
import KeyIcon from '@material-ui/icons/VpnKey';
import HomeIcon from '@material-ui/icons/Home';

import AddExercisePage from '../pages/AddExercise';
import ViewExercisePage from '../pages/ViewExercise';
import PasswordPage from '../pages/Password';
import PasswordModal from './PasswordModal';

export const drawerWidth = 240;

const styles = theme => ({
  toolbar: theme.mixins.toolbar,
  drawerPaper: {
    width: drawerWidth,
  }
});

class Exercise extends React.Component {
  showLocked() {
    alert('Exercise is locked');
  }
  render() {
    const { title, locked, onClick } = this.props;
    return (
      <ListItem button onClick={!!locked ? () => this.showLocked() : onClick}>
        <ListItemIcon>{!!locked ? <LockIcon /> : <LockOpenIcon />}</ListItemIcon>
        <ListItemText primary={title} />
        {/* <ListItemSecondaryAction>
          {!!locked ? <LockIcon /> : <LockOpenIcon /> }
        </ListItemSecondaryAction> */}
      </ListItem>
    );
  }
}

class AppDrawer extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      displayPasswordModal: false
    };
  }
  addExercise() {
    this.props.navigate({
      title: 'Add Exercise',
      content: <AddExercisePage />
    });
  }

  deleteExercise() {
    this.props.navigate({
      title: 'Delete Exercise',
      content: <div>Under construction</div>
    });
  }

  viewExercise({title, exercise}) {
    this.props.navigate({
      title,
      content: <ViewExercisePage exercise={exercise} />,
    })
  }

  enterPassword() {
    this.props.navigate({
      title: 'Enter Password',
      content: <PasswordPage />
    })
    // this.setState({
    //   displayPasswordModal: true
    // });
  }

  dismissPasswordModal() {
    this.setState({
      displayPasswordModal: false
    });
  }

  renderAdminOptions() {
    return (
      <>
        <ListItem button onClick={() => this.addExercise()}>
          <ListItemIcon><AddIcon /></ListItemIcon>
          <ListItemText primary="Add Exercise" />
        </ListItem>
        <ListItem button onClick={() => this.deleteExercise()}>
          <ListItemIcon><AddIcon /></ListItemIcon>
          <ListItemText primary="Delete Exercise" />
        </ListItem>
      </>
    )
  }

  render() {
    const { displayPasswordModal } = this.state;
    const { classes, theme, isAdmin, exerciseReducer: { exercises = [] } = {} } = this.props;
    const drawer = (
      <div>
        <div className={classes.toolbar}>
          <List>
            <ListItem button onClick={() => this.props.viewHome()}>
              <ListItemIcon><HomeIcon /></ListItemIcon>
              <ListItemText primary="Home" />
            </ListItem>            
          </List>
        </div>
        <Divider />
        <List subheader={<ListSubheader component="div">Exercises</ListSubheader>}>
          {exercises.map(({title, exercise}, index) => (
            <Exercise title={title} key={`${title}-${index}`} locked={index > 1} onClick={() => this.viewExercise({title, exercise})}/>
          ))}
        </List>
        <Divider />
        <List>
          <ListItem button onClick={() => this.enterPassword()}>
            <ListItemIcon><KeyIcon /></ListItemIcon>
            <ListItemText primary="Enter Password" />
          </ListItem>
          {!!isAdmin ? this.renderAdminOptions() : undefined}
          
        </List>
      </div>
    );
    return (
      <>
        <Hidden smUp implementation="css">
        <Drawer
              container={this.props.container}
              variant="temporary"
              anchor={theme.direction === 'rtl' ? 'right' : 'left'}
              open={this.props.drawerOpen}
              onClose={() => this.toggleDrawer(false)}
              classes={{
                paper: classes.drawerPaper,
              }}
            >
            {drawer}
          </Drawer>
        </Hidden>
        <Hidden xsDown implementation="css">
          <Drawer
            classes={{
              paper: classes.drawerPaper,
            }}
            variant="permanent"
            open
          >
            {drawer}
          </Drawer>
        </Hidden>
        <PasswordModal open={displayPasswordModal} close={() => this.dismissPasswordModal()} />
      </>
    )
  }
}

const mapStateToProps = state => ({
  ...state
});

const mapDispatchToProps = dispatch => ({
  // navigatePage: ({id, title, content}) => dispatch(navigatePage({title, exercise}))
});

export default withStyles(styles, { withTheme: true })(connect(mapStateToProps, mapDispatchToProps)(AppDrawer));
