import React from 'react';

class Mission extends React.Component {
  indexToMissionName(index) {
    const letter = String.fromCharCode(97 + index).toUpperCase();
    return <strong>Mission {letter}</strong>
  }
  render() {
    const { description, points, index } = this.props; 
    return <li>{this.indexToMissionName(index)} {description} <strong>({points} Nectar Points)</strong></li>
  }
}

class Missions extends React.Component {
  render() {
    const { missions } = this.props;
    return (
      <ul>
        {missions.map(({description, points}, index) => <Mission key={description} index={index} description={description} points={points} />)}
      </ul>
    )
  }
}

class ViewExercise extends React.Component {
  render() {
    const { exercise: { title, missions } } = this.props;
    return (
      <>
      <h5>You must complete AT LEAST ONE of the following missions to summon Hermes to bring news of the next missions.</h5>
      <Missions title={title} missions={missions} />
      </>
    );
    
  }
}

export default ViewExercise;