import { default as allExercises } from '../assets/exercises';
import _ from 'lodash';

export default (state = {}, action) => {
  const { exercises = [] } = state;
  switch (action.type) {
    case 'ADD_EXERCISE':
      console.log('MZ111', action, exercises.concat(action.payload));
      return {
        ...state,
        exercises: exercises.concat(action.payload)
      }
    case 'GET_EXERCISES':    
      return state;
    case 'UNLOCK_EXERCISE':
      const {payload: newPassword = ''} = action;
      const newExerciseIndex = _.findIndex(allExercises, ({password}) => {console.log(password); return !!password && password.toUpperCase() === newPassword.toUpperCase();});
      if (newExerciseIndex !== -1) {
        return {
          ...state,
          exercises: allExercises.slice(newExerciseIndex),
          latestPassword: newPassword
        }
      }
      return state;
   default:
    return state
  }
 }