import React from 'react';
import { connect } from 'react-redux';

import { withStyles } from '@material-ui/core/styles';
import { Button, TextField } from '@material-ui/core';

import cn from 'classnames';

import { addExercise, getExercises } from '../actions/exerciseActions';

const styles = theme => ({
    container: {
      display: 'flex',
      flexWrap: 'wrap'
    },
    flex: {
      flexGrow: 1,
      flexShrink: 1
    },
    textField: {
      marginLeft: theme.spacing.unit,
      marginRight: theme.spacing.unit,
    },
    dense: {
      marginTop: 16,
    },
    menu: {
      width: 200,
    },
    button: {
      margin: theme.spacing.unit,
    },
    vBox: {
      display: 'flex',
      flexDirection: 'column',
      flexGrow: 1,
      flexShrink: 1
    },
    hBox: {
      display: 'flex',
      flexDirection: 'row',
      flexGrow: 1,
      flexShrink: 1
    }
  });
  
class AddActivity extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      title: '',
      exercise: '',
      authenticated: false,
      password: ''
    };
  }
  handleChange = name => event => {
    this.setState({
      [name]: event.target.value,
    });
  };

  renderAuthenticated() {
    const { classes } = this.props;
    const { title, exercise } = this.state;
    return (
    <form className={cn(classes.container, classes.flex)} noValidate autoComplete="off">
      <div className={classes.vBox}>
        <div className={classes.hBox}>
          <TextField
            id="outlined-name"
            label="Title"
            placeholder="My Activity..."
            className={cn(classes.textField, classes.flex)}
            value={title}
            onChange={this.handleChange('title')}
            margin="normal"
            variant="outlined"
            />
          <Button color="primary" className={classes.button} onClick={() => this.props.addExercise({ title, exercise })}>
            Save
          </Button>
        </div>
        
        <TextField
          id="outlined-textarea"
          label="Exercise"
          placeholder="Enter the exercise..."
          multiline
          className={cn(classes.textField, classes.flex)}
          value={exercise}
          onChange={this.handleChange('exercise')}
          margin="normal"
          variant="outlined"
        />
        <pre>
        {
          JSON.stringify(this.props.exerciseReducer)
        }
        </pre>
      </div>
    </form>
    );
  }

  login() {
    this.setState({
      authenticated: this.state.password === 'hello',
      password: ''
    });
  }

  renderLogin() {
    const { classes } = this.props;
    return (
    <form className={classes.container} noValidate autoComplete="off">
        <TextField
        id="password"
        label="Password"
        type="password"
        className={cn(classes.textField, classes.flex)}
        value={this.state.password}
        onChange={this.handleChange('password')}
        margin="normal"
        variant="outlined"
        />
    <Button color="primary" className={classes.button} onClick={() => this.login()} value="submit">
        Login
    </Button>
    </form>
    );
  }

  renderUnauthenticated() {
    return (<div>You are not authorised to view this</div>);
  }

  render() {
    //!!this.state.authenticated
    return true ? this.renderAuthenticated() : this.renderLogin();
  }
}

const mapStateToProps = state => ({
  ...state
});

const mapDispatchToProps = dispatch => ({
  addExercise: ({title, exercise}) => dispatch(addExercise({title, exercise})),
  getExercises: () => dispatch(getExercises())
});

export default withStyles(styles, { withTheme: true })(connect(mapStateToProps, mapDispatchToProps)(AddActivity));